import React from 'react';
import Button from '@material-ui/core/Button';

const Popup = props => {
  return(
    <div className={`${props.class} popup`}>
      <button className="close" onClick={props.onClick}>&#10006;</button>
      <p>
      This site uses cookies. By continuing to use this site,
      closing the banner ot clicking "I Agree" to the use of cookies.
      </p>
      <Button variant="contained" onClick={props.onClick} className="btn-close" color="primary">
        I Agree
      </Button>
    </div>
  )
}

export default Popup