import React from 'react';
import Container from '@material-ui/core/Container';

const Privacy = () => {
  return(
    <Container maxWidth="lg">
      <h1>Privacy Policy</h1>
      <h3>А. Responsible party</h3>

      <p>The responsible party for the purposes of the General Data Protection Regulation 
        and other national data protection laws of the European Member States as well a
        s other data protection regulations for the processing of personal 
        data within the context of the “androidpit.de”, “androidpit.com”, “androidpit.it”, 
        “androidpit.es”, “androidpit.fr” and “androidpit.com.br” portal 
        (hereinafter referred to as “the portal”) is:
      </p>
      <address>
        NextPit GmbH<br/>
        Stille Straße 18<br/>
        13156 Berlin<br/>
        Germany<br/>
        Email: support@androidpit.com<br/>
        Website: www.androidpit.com<br/>
      </address>
      <br/>
      <br/>
      <br/>
      <h3>B. Contact details of the data protection representative</h3>

      <p>
        The data protection representative of the responsible party can be reached as follows:
      </p>

      <address>
      Data protection representative of NextPit GmbH<br/>
      Stille Straße 18<br/>
      13156 Berlin<br/>
      Germany<br/>
      Email: support@androidpit.com<br/>
      C. Processing of personal data<br/>
      </address>
      <br/><br/><br/>

    <h3>The following terms are used in the following data protection policy:</h3>

    <p>Visitors: Persons who access the portal without registering 
      (no use of the portal's functions and services is possible).
    </p>
    <p>
      Users: Persons who have registered to use the portal and use the portal within the context of a user agreement on the basis of the responsible party's terms of use. 
    </p>

    <p>
      There is a contact form on our website, 
      which can be used for electronic communication. 
      If a visitor takes advantage of this option, 
      the data entered in the input form will be transferred to us and saved. 
      This data includes:
    </p>
    <ul>
      <li>Name</li>
      <li>Email address</li>
      <li>The message</li>
    </ul>
    <br/>
    <br/>
    <br/>
    </Container>
  )
}

export default Privacy